document.getElementById("btn23-2").onclick = () => {
    //2.–ó–∞–ø—Ä–æ—Å–∏—Ç—å —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è —á–∏—Å–ª–æ –æ—Ç 0 –¥–æ 9 –∏ –≤—ã–≤–µ—Å—Ç–∏ –µ–º—É —Å–ø–µ—Ü—Å–∏–º–≤–æ–ª,
    // –∫–æ—Ç–æ—Ä—ã–π —Ä–∞—Å–ø–æ–ª–æ–∂–µ–Ω –Ω–∞ —ç—Ç–æ–π –∫–ª–∞–≤–∏—à–µ (1‚Äì!, 2‚Äì@, 3‚Äì# –∏ —Ç. –¥).
    let inputKey = Number(
        prompt(`Enter number from 0 to 9, than return symbol for this number`)
    );

    switch (true) {
        case inputKey == 1:
            alert(`your symbol = "!"`);
            break;
        case inputKey == 2:
            alert(`your symbol = "@"`);
            break;
        case inputKey == 3:
            alert(`your symbol = "#"`);
            break;
        case inputKey == 4:
            alert(`your symbol = "$"`);
            break;
        case inputKey == 5:
            alert(`your symbol = "%"`);
            break;
        case inputKey == 6:
            alert(`your symbol = "^"`);
            break;
        case inputKey == 7:
            alert(`your symbol = "&"`);
            break;
        case inputKey == 8:
            alert(`your symbol = "*"`);
            break;
        case inputKey == 9:
            alert(`your symbol = "("`);
            break;
        case inputKey == 0:
            alert(`your symbol = ")"`);
            break;
        default:
            alert(`try again`);
            break;
    }
};
