document.getElementById("btn23-6").onclick = () => {
    //6.–ù–∞–ø–∏—Å–∞—Ç—å –∫–æ–Ω–≤–µ—Ä—Ç–æ—Ä –≤–∞–ª—é—Ç. –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –≤–≤–æ–¥–∏—Ç –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ USD, –≤—ã–±–∏—Ä–∞–µ—Ç,
    //–≤ –∫–∞–∫—É—é –≤–∞–ª—é—Ç—É —Ö–æ—á–µ—Ç –ø–µ—Ä–µ–≤–µ—Å—Ç–∏ EUR, UAN –∏–ª–∏ AZN, –∏ –ø–æ–ª—É—á–∞–µ—Ç –≤ –æ—Ç–≤–µ—Ç —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â—É—é —Å—É–º–º—É.
    let inputUsd = +prompt("Eter how much dollars you want to convert");
    let inputCurrency = prompt(
        "Enter for choise (EUR UAH AZN or eur uah azn) please"
    );
    const usdToEur = 0.7;
    const usdToUah = 27.3;
    const usdToAzn = 158;
    switch (true) {
        case inputCurrency == "EUR" || inputCurrency == "eur":
            let convertEur = inputUsd * usdToEur;
            alert(convertEur);
            break;
        case inputCurrency == "UAH" || inputCurrency == "uah":
            let convertUah = inputUsd * usdToUah;
            alert(convertUah);
            break;
        case inputCurrency == "AZN" || inputCurrency == "azn":
            let convertAzn = inputUsd * usdToAzn;
            alert(convertAzn);
            break;
        default:
            alert("Try again please");
            break;
    }
};
