document.getElementById("btn23-9").onclick = () => {
    //9.–ó–∞–¥–∞—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é 3 –≤–æ–ø—Ä–æ—Å–∞, –≤ –∫–∞–∂–¥–æ–º –≤–æ–ø—Ä–æ—Å–µ –ø–æ 3 –≤–∞—Ä–∏–∞–Ω—Ç–∞ –æ—Ç–≤–µ—Ç–∞. –ó–∞ –∫–∞–∂–¥—ã–π –ø—Ä–∞–≤–∏–ª—å–Ω—ã–π –æ—Ç–≤–µ—Ç –Ω–∞—á–∏—Å–ª—è–µ—Ç—Å—è 2 –±–∞–ª–ª–∞.
    //–ü–æ—Å–ª–µ –≤–æ–ø—Ä–æ—Å–æ–≤ –≤—ã–≤–µ–¥–∏—Ç–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –Ω–∞–±—Ä–∞–Ω–Ω—ã—Ö –±–∞–ª–ª–æ–≤.
    let z = 0;
    for (let i = 0; i < 3; i++) {
        alert(`how much ${i} + ${i} ?, true = 2 points`);
        let x = i + i;
        confirm(`${i} + ${i} = ${x + 1}`);
        confirm(`${i} + ${i} = ${x + 2}`);
        let rightAnswer = confirm(`${i} + ${i} = ${x}`);
        if (rightAnswer == true) {
            rightAnswer = 2;
            z = z + rightAnswer;
        }
        console.log(`${rightAnswer} and ${z}`);
    }
    alert(`you have ${z} points`);
};
